body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 950px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

/*
.debug{
    .debug(rgba(0, 0, 0, 0.5), 1px #ff0 solid);
}
*/

h2,
.h2 {
  font-size: 2.5rem;
  text-align: center;
  margin: 20px 0;
}

.h2sub {
  text-align: center;
  font-size: 20px;
  margin: 20px 0;
}

.profit {
  background: #232324;
  padding: 15px 0;
}

.profit__title {
  color: #fff;
}

.profit__subtitle {
  color: #6cc372;
}

.profitItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.575%;
  margin-right: -1.575%;
  justify-content: space-around;
}

.profitItem {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.575%;
  margin-right: 1.575%;
  width: 30.18333333%;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.profitItem__img {
  border-radius: 50%;
}

.profitItem__subtitle {
  color: #fff;
  font-size: 1.5rem;
  margin: 3px 0;
}

.profitItem__text {
  font-size: 16px;
  color: #d94948;
}

.humans {
  background: #6cc372;
  padding: 10px 0;
}

.humans h2 {
  color: #232324;
}

.humans .h2sub {
  color: #fff;
}

.humans .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.575%;
  margin-right: -1.575%;
  justify-content: center;
}

.humans .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.575%;
  margin-right: 1.575%;
  width: 21.85%;
  border-radius: 10px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 10px;
  text-align: center;
  background: #fff;
  display: flex;
  flex-direction: column;
}

.humans .items .item img {
  margin: 10px auto;
  width: 90%;
  align-self: center;
}

.humans .items .item .subtitle {
  color: #222;
  font-size: 24px;
  margin: 10px;
}

.humans .items .item .job {
  margin: auto 10px 10px 10px;
}

.humans .items .item .follow {
  background: #6cc372;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
}

.portfolio {
  background: #f0f0f0;
  padding: 30px 0 170px;
}

.portfolio h2 {
  color: #232324;
}

.portfolio .h2sub {
  color: #6cc372;
}

.portfolio .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.575%;
  margin-right: -1.575%;
  justify-content: space-between;
}

.portfolio .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.575%;
  margin-right: 1.575%;
  width: 18.62083333%;
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: center;
}

.portfolio .items .item img {
  margin: 10px auto;
  width: 100%;
}

.portfolio .items .item .txt {
  color: #232324;
  font-size: 20px;
}

.pricing .h2sub {
  color: #eb4f4e;
}

.pricing .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.575%;
  margin-right: -1.575%;
  justify-content: center;
  align-items: flex-end;
  margin-top: -155px;
}

.pricing .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.575%;
  margin-right: 1.575%;
  width: 30.18333333%;
  text-align: center;
  border-radius: 10px;
  overflow: hidden;
}

.pricing .items .item .title {
  background: #fff;
  overflow: hidden;
  background: #6cc372;
}

.pricing .items .item ul li {
  background: #f0f0f0;
  padding: 15px 0;
  border-bottom: 1px solid #232324;
}

.pricing .items .item .price {
  background: #eb4f4e;
  color: #fff;
  padding: 10px 15px;
}

.pricing .items .item .price .cur {
  font-size: 1.5em;
}

.pricing .items .item .price .val {
  font-size: 3em;
}

.pricing .items .item .price .per {
  font-size: 1.2em;
  margin-top: 10px;
}

.pricing .items .item .price a {
  color: #232324;
  text-decoration: none;
  background: #fff;
  padding: 5px 20px;
  border-radius: 5px;
  margin-left: auto;
}

.some {
  padding: 20px 0;
}

.some .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.575%;
  margin-right: -1.575%;
}

.some .items .item {
  word-wrap: break-word;
  padding-left: 1.575%;
  padding-right: 1.575%;
  min-height: 200px;
}

.some .items .item-left {
  background: #6cc372;
  width: 30.18333333%;
  /*
                padding-right: @offset;

                padding-left: 0;
                margin-left: @offset_one_side;*/
}

.some .items .item-right {
  background: #eb4f4e;
  width: 63.51666667%;
  /*
                padding-left: 0;

                padding-right: 0;
                margin-right: @offset_one_side;*/
}

footer .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.575%;
  margin-right: -1.575%;
}

footer .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.575%;
  margin-right: 1.575%;
  width: 30.18333333%;
  background: #aaa;
  min-height: 200px;
}

footer .items .item .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.575%;
  margin-right: -1.575%;
}

footer .items .item .row div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1.575%;
  margin-right: 1.575%;
  width: 96.85%;
  background: #f50;
  margin-bottom: 10px;
}

@media screen and (min-width: 620px) {
  .humans .items .item .subtitle {
    color: #f90;
  }
}

@media screen and (max-width: 992px) {
  .profitItem {
    width: 38.51666667%;
  }

  .humans .items .item {
    width: 38.51666667%;
  }

  .portfolio {
    padding: 30px 0;
  }

  .portfolio .items {
    justify-content: center;
  }

  .portfolio .items .item {
    width: 38.51666667%;
  }

  .pricing .items {
    margin-top: 0px;
  }

  .pricing .items .item {
    width: 100%;
    max-width: 450px;
    margin-top: 30px;
  }

  footer .items .item {
    width: 96.85%;
  }

  footer .items .item .row div {
    width: 30.18333333%;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .profitItem {
    width: 96.85%;
  }

  .humans .items .item {
    width: 46.85%;
  }

  .portfolio .items .item {
    width: 46.85%;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .profitItem__subtitle {
    color: #000;
    background: #fff;
    margin: 20px auto;
    padding: 10px 0;
    max-width: 200px;
  }

  .humans .items .item {
    width: 80.18333333%;
  }

  .portfolio .items .item {
    width: 80.18333333%;
  }

  footer .items .item .row div {
    width: 96.85%;
  }
}

@media screen and (max-width: 520px) {
  .humans .items .item .subtitle {
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 400px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .profitItems {
    margin-left: -0.5%;
    margin-right: -0.5%;
  }

  .profitItem {
    margin-left: 0.5%;
    margin-right: 0.5%;
  }

  .profitItem {
    width: 99%;
  }

  .humans .items {
    margin-left: -0.5%;
    margin-right: -0.5%;
  }

  .humans .items .item {
    margin-left: 0.5%;
    margin-right: 0.5%;
  }

  .humans .items .item {
    width: 99%;
  }

  .portfolio .items {
    margin-left: -0.5%;
    margin-right: -0.5%;
  }

  .portfolio .items .item {
    margin-left: 0.5%;
    margin-right: 0.5%;
  }

  .portfolio .items .item {
    width: 99%;
  }

  .pricing .items {
    margin-left: -0.5%;
    margin-right: -0.5%;
  }

  .pricing .items .item {
    margin-left: 0.5%;
    margin-right: 0.5%;
  }

  .some .items {
    margin-left: -0.5%;
    margin-right: -0.5%;
  }

  .some .items .item {
    padding-left: 0.5%;
    padding-right: 0.5%;
  }

  .some .items .item-left {
    width: 32.33333333%;
  }

  .some .items .item-right {
    width: 65.66666667%;
  }

  footer .items {
    margin-left: -0.5%;
    margin-right: -0.5%;
  }

  footer .items .item {
    margin-left: 0.5%;
    margin-right: 0.5%;
  }

  footer .items .item {
    width: 99%;
  }

  footer .items .item .row {
    margin-left: -0.5%;
    margin-right: -0.5%;
  }

  footer .items .item .row div {
    margin-left: 0.5%;
    margin-right: 0.5%;
  }

  footer .items .item .row div {
    width: 99%;
  }
}

@media screen and (min-width: 520px) and (max-width: 620px) {
  .humans .items .item img {
    transform: rotate(20deg);
  }
}